//*mixins*//
@import "./mixin.scss";

.page__gallery {
   &--title {
      text-align: center;
      margin-bottom: 3.5rem;
      h3 {
         font-family: $font-secondary;
         font-weight: 600;
         font-size: 3rem;
         color: $black;
      }
   }
   &--row {
      width: 100%;
   }
   &--line {
      margin-top: 6rem;
      hr {
         margin: 0;
         width: 100%;
         height: 1px;
         border: none;
         border-color: transparent;
         background-color: $secondary-color-two;
      }
   }
}
.box__gallery {
   width: 100%;
   display: flex;
   flex-wrap: wrap;
   align-items: flex-start;
   justify-content: space-between;
   a {
      display: block;
      width: 25%;
      position: relative;
      text-decoration: none;
      overflow: hidden;
      border: 4px solid $secondary-color;
      transition: opacity 0.3s linear;
      img {
         transition: transform 0.3s linear;
      }
      &::after {
         content: "";
         background: transparent url(../img/gallery__page/eye-icon.png) no-repeat center;
         position: absolute;
         margin: auto;
         top: 0;
         right: 0;
         bottom: 0;
         left: 0;
         opacity: 0;
         visibility: hidden;
         transition: opacity 0.3s linear, visibility 0.3s linear;
      }
      &:hover {
         opacity: 0.7;
         img {
            transform: scale(1.1);
         }
         &::after {
            opacity: 1;
            visibility: visible;
         }
      }
   }
}
.gallery__img--title {
   font-family: $font-secondary;
   font-weight: 600;
}
